.menu {
  position: relative;
}

.menu__button {
  position: absolute;
  right: 0;
  top: -48px;
  font-size: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.page__no-js .menu__button {
  display: none;
}

.menu__icon {
  display: block;
  width: 25px;
  height: 19px;
  background-repeat: no-repeat;
}

.menu__icon--open {
  background-image: url("../img/icon-mobile-menu.svg");
}

.menu__icon--close {
  background-image: url("../img/icon-close.svg");
}

.menu--open .menu__icon--close,
.menu--close .menu__icon--open {
  display: block;
}

.menu--open .menu__icon--open,
.menu--close .menu__icon--close {
  display: none;
}

.menu__list {
  list-style: none;
  min-width: 320px;
  margin: 0 auto;
  padding: 0;
}

.menu--open .menu__list {
  display: block;
}

.menu--close .menu__list {
  display: none;
}

.menu__link {
  display: block;
  padding: 24px;
  border-top: 1px solid $grey;

  text-decoration: none;
  color: $black-dark;
  font-family: $oswald;
  font-size: $font-size-larger;
  font-weight: 500;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  vertical-align: center;
  cursor: pointer;
}

@media (min-width: $tablet-width) {
  .menu__button {
    display: none;
  }

  .menu {
    //width: 438px;
  }

  .menu .menu__list {
    display: flex;
    justify-content: space-between;
    width: auto;
  }

  .menu__link {
    padding: 12px 0;

    font-size: $font-size-large;
    line-height: 25px;
    color: $black;

    border-top: none;
    transition: 200ms ease;
  }

  .menu__link:hover,
  .menu__link:focus {
    opacity: 0.6;
    outline: none;
  }

  .menu__link:active {
    opacity: 0.3;
  }

  .menu__item {
    margin-right: 36px;
  }

  .menu__item:last-of-type {
    margin-right: 0;
  }

  .menu__link--active {
    border-bottom: 2px solid $green;
  }
}

@media (min-width: $desktop-width) {
  .menu {
    position: static;
  }

  .menu__list--green::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    z-index: -5;

    display: block;
    width: 50vw;
    height: 696px;

    background-color: $green-dark;
  }

  .menu__list--green .menu__link {
    color: $white;
  }

  .menu__list--green .menu__link--active {
    border-bottom: 2px solid $white;
  }

  .menu__link {
    font-size: $font-size-larger;
  }
}
